import React from "react";
import { Link, animateScroll as scroll } from "react-scroll";

function AboutContent() {
  return (
    <div>
      <p className="about__paragraph">
         -. 
            :    -.
      </p>
      <p className="about__paragraph">
                  . 
            &nbsp;
        <Link 
          smooth={true} 
          offset={0} 
          duration={800} 
          to='portfolio'
          className="about__link"
        >
          
        </Link>.
        </p>
        <p className="about__paragraph">
                -, 
                   .
        </p>
        <p className="about__paragraph">
              ,      ,  &nbsp;
          <Link 
            smooth={true} 
            offset={0} 
            duration={800} 
            to='contacts'
            className="about__link"
          >
              
          </Link>,
              .
        </p>
    </div>
  );
}

export default AboutContent;